import React from 'react';

interface ThemeSvgGradientProps {
  id: string,
}

export const ThemeSvgGradient = ({ id }: ThemeSvgGradientProps) => (
  <linearGradient id={id} x1="0" y1="0" x2="100%" y2="100%" gradientUnits="userSpaceOnUse">
    <stop offset="9.37%" stopColor="#9BF4F4" />
    <stop offset="90.62%" stopColor="#DA47FF" />
  </linearGradient>
);
